import { useEffect, useState } from "react"
import "../assets/css/style.css"
import { initializeApp } from "firebase/app";
import {getFirestore, collection, addDoc, onSnapshot, doc, deleteDoc } from 'firebase/firestore'
import {VscTrash} from "react-icons/vsc"


export default function Version(){
    const firebaseConfig = {
        apiKey: process.env.REACT_APP_API_KEY,
        authDomain: process.env.REACT_APP_AUTH_DOMAIN,
        projectId: process.env.REACT_APP_PROJECT_ID,
        storageBucket: process.env.REACT_APP_STORAGE_BUCKET,
        messagingSenderId: process.env.REACT_APP_MESSAGING_SENDER_ID,
        appId: process.env.REACT_APP_ID
      };

    const app = initializeApp(firebaseConfig);
    const db = getFirestore(app)
    

    const [data , setData] = useState([])
    const [saveData, setSaveData] = useState([])
    const [deleteKey , setDeleteKey] = useState([])
    useEffect(() => {
        onSnapshot(collection(db,"version"),(snapshot) => {
            const data = snapshot.docs.map(doc => ({
                id: doc.id,
                ...doc.data()
            }))
            setData(data)
        })
    },[] )

    function saveKayit(){
        const ekranAdi = document.getElementById("ekranAdi").value
        const aciklama = document.getElementById("aciklama").value        
        const status = document.getElementById("status").value
        const gelitirilmeTarihi = document.getElementById("gelitirilmeTarihi").value

        const data = {
            screen: ekranAdi,
            description: aciklama,
            status: status,
            date: gelitirilmeTarihi,
        }
        setSaveData(prev => [...prev,data])
        addDoc(collection(db,"version"),data)
        document.getElementById("surumAdd").style.display = "none"
    }
    function deleteData(index){
        if(document.getElementById("password").value === process.env.REACT_APP_PASSWORD){
            const id = data[index].id
            deleteDoc(doc(db,"version",id))
            document.getElementById("deleteAccepted").style.display = "none"
        }
        else{
            alert("ifre Hatal")
        }

    }
    function openPopup(index){
        document.getElementById("deleteAccepted").style.display = "block"
        setDeleteKey(index)
    }

    function openKayit(){
        document.getElementById("surumAdd").style.display = "block"
    }
   
    
    return(
        <div>
            <h2 className="text-2xl my-4">Anadolu Sigorta Srm Kontrol Ekran</h2>

            <button onClick={() => openKayit()} className="mx-4 p-4 bg-slate-800 text-white rounded-md">Srm Girii Olutur</button>
            <table className="my-6 w-full">
                <thead>
                    <tr className="bg-slate-200">
                        <th className="p-4">ID</th>
                        <th className="p-4">Ekran Ad</th>
                        <th className="p-4">Aklama</th>
                        <th className="p-4">Status</th>
                        <th className="p-4">Gelitirilme Tarihi</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {data.map((item,index) => (
                        <tr key={index}>
                            <td className="p-4 text-center">{index+1}</td>
                            <td className="p-4 text-center">{item.screen}</td>
                            <td className="p-4 text-center">{item.description}</td>
                            <td className="p-4 text-center">{item.status}</td>                            
                            <td className="p-4 text-center">{item.date}</td>
                            <td><button onClick={() => openPopup(index)}><VscTrash/></button></td>                        
                        </tr>
                    ))}
                    
                </tbody>
            </table>

            <div id="surumAdd">
                <h2 className="text-2xl my-4">Anadolu Sigorta Srm Girii</h2>
                    
                <div className="my-6 w-full">
                    <div className="flex flex-col">
                        <label className="text-lg">Ekran Ad</label>
                        <input id="ekranAdi" type="text" className="p-4 bg-slate-200 rounded-md"/>
                    </div>
                    <div className="flex flex-col">
                        <label className="text-lg">Aklama</label>
                        <input id="aciklama"type="text" className="p-4 bg-slate-200 rounded-md"/>
                    </div>
                    <div className="flex flex-col">
                        <label className="text-lg">Status</label>
                        <input id="status" type="text" className="p-4 bg-slate-200 rounded-md"/>
                    </div>
                    <div className="flex flex-col">
                        <label className="text-lg">Gelitirilme Tarihi</label>
                        <input id="gelitirilmeTarihi" type="text" className="p-4 bg-slate-200 rounded-md"/>
                    </div>
                    <button onClick={() => saveKayit()} className="m-4 p-4 bg-slate-800 text-white rounded-md">Kaydet</button>

                </div>
            </div>
            <div>
                <div id="deleteAccepted">
                    <label>ifreyi Giriniz</label>
                    <input id="password" type="password"/>
                    <button onClick={() => deleteData(deleteKey)}>Sil</button>
                </div>
            </div>
        </div>
    )
}